.main-screen {
  background-image: url(../images/main-screen.png);
  background-color: $light-grey;
  background-size: cover;
  width: 100%;
  height: 100vh;
  min-height: 700px;
  color: #fff;
  position: relative;
  @media(max-width: 480px) {
    min-height: 590px;
  }
  &_wrap {
    width: 100%;
    height: 100%;
    position: relative;
  }
  &_text {
    position: absolute;
    bottom: 115px;
    width: calc(100% - 20px);
    z-index: 2;
    left: 20px;
    @media (max-width: 768px) {
      bottom: 60px;
    }
    @media (max-width: 480px) {
      bottom: 0px;
    }
  }
  &_title {
    font-size: 50px;
    line-height: 140%;
    margin-bottom: 100px;
    max-width: 620px;
    font-weight: 300;
    padding-right: 20px;
    @media (max-width: 1024px) {
      font-size: 40px;
    }
    @media (max-width: 768px) {
      margin-bottom: 50px;
    }
    @media (max-width: 480px) {
      font-size: 30px;
    }
  }
}
.main-video {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  object-fit: cover;
  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    @media (max-width: 480px) {
      display: none;
    }
  }
  img {
    display: none;
    @media (max-width: 480px) {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
  &:after {
    content: '';
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 1;
    top: 0;
    left: 0;
  }
}

.numbers {
 width: 100%;
 display: flex;
 flex-wrap: wrap;
}
.number-item {
  width: 25%;
  border-left: 4px solid $red;
  padding: 0 38px;
  @media (max-width: 1300px) {
    padding: 0 20px;
  }
  @media (max-width: 1024px) {
    width: 50%;
    margin-bottom: 40px;
  }
  @media (max-width: 480px) {
    border-left: 2px solid $red;;
  }
  &_title {
    font-size: 2.875rem;
    line-height: 2.875rem;
    font-weight: 600;
    margin-bottom: 20px;
    @media (max-width: 1300px) {
      font-size: 2rem;
      margin-bottom: 10px;
    }
  }
  &_subtitle {
    text-transform: uppercase;
    font-size: 0.875rem;
  }
}

.services {
  padding-top: 116px;
  padding-bottom: 116px;
  @media (max-width: 768px) {
    padding-bottom: 80px;
  }
  @media (max-width: 480px) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
  &_list {
    display: grid;
    // grid-template-rows: auto 1fr;
    // grid-template-columns: repeat(4, 340px);
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1vw;
    grid-template-areas:
    "s1 s2 s2 s2"
    "s3 s3 s4 s4"
    "s5 s6 s7 s8";
    @media (max-width: 1200px) {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    @media (max-width: 480px) {
      display: block;
    }
  }
}
.services-item {
  background-color: #333;
  height: 340px;
  // width: 340px;
  // border: 1px solid #fff;
  display: block;
  color: #fff;
  text-decoration: none;
  padding: 50px 40px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  position: relative;
  @media (max-width: 1200px) {
    width: 49%;
    margin-bottom: 10px;
  }
  @media (max-width: 480px) {
    width: 100%;
    height: 220px;
    padding: 30px 20px;
  }
  &:nth-child(1) {
    grid-area: s1;
  }
  &:nth-child(2) {
    grid-area: s2;
    transition-delay: 0.2s;
    @media (max-width: 480px) {
      transition-delay: 0s;
    }
  }
  &:nth-child(3) {
    grid-area: s3;
  }
  &:nth-child(4) {
    grid-area: s4;
    transition-delay: 0.2s;
    @media (max-width: 480px) {
      transition-delay: 0s;
    }
  }
  &:nth-child(5) {
    grid-area: s5;
  }
  &:nth-child(6) {
    grid-area: s6;
    transition-delay: 0.2s;
    @media (max-width: 480px) {
      transition-delay: 0s;
    }
  }
  &:nth-child(7) {
    grid-area: s7;
    transition-delay: 0.3s;
    @media (max-width: 480px) {
      transition-delay: 0s;
    }
  }
  &:nth-child(8) {
    grid-area: s8;
    transition-delay: 0.4s;
    @media (max-width: 480px) {
      transition-delay: 0s;
    }
  }
  &_icon {
    z-index: 10;
    position: relative;
    transform-origin: left;
    @media (max-width: 480px) {
      transform: scale(0.8);
    }
    svg {
      fill: #fff;
    }
  }
  &_title {
    font-size: 1.5rem;
    font-weight: 600;
    z-index: 10;
    transition: all 0.2s ease;
    @media (max-width: 1400px) {
      font-size: 1.25rem;
    }
  }
  &_img {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      transition: all 0.2s ease;
    }
    &:before {
      content: '';
      width: 100%;
      height: 100%;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba(0,0,0,0.5);
      transition: all 0.2s ease;
      z-index: 2;
    }
  }
  &:hover {
    .services-item_img img {
      transform: scale(1.2);
    }
    .services-item_img:before {
      background-color: rgba(0,0,0,0.6);
    }
    // .services-item_title {
    //   color: $yellow;
    // }
    .services-item_icon svg {
      fill: $red;
    }
  }
  &.block-nav {
    @media (max-width: 1200px) {
      width: 49%;
      min-width: 0;
    }
    @media (max-width: 480px) {
      width: 100%;
      height: 220px;
    }
  }
}

.block-nav {
  background-color: $yellow!important;
  display: flex!important;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 340px;
  min-width: 340px;
  height: 340px;
  padding: 20px;
  text-align: center;
  &:hover {
    background-color: lighten($yellow, 8%)!important;
  }
  &_title {
    color: #000;
    font-size: 2.25rem;
    font-weight: 600;
    text-transform: uppercase;
    width: 100%;
  }
  &_link {
    display: block;
    margin-top: 30px;
    color: $red;
    font-size: 1rem;
    font-weight: 400;
    &:hover {
      text-decoration: none;
    }
  }
}

.advantages {
  padding-bottom: 50px;
  &_wrap {
    display: flex;
    flex-wrap: wrap;
  }
}
.advantages-item {
  width: 33%;
  display: flex;
  align-items: center;
  padding: 40px 40px;
  position: relative;
  perspective: 900px;
  perspective-origin: center;
  @media (max-width: 1024px) {
    width: 50%;
  }
  @media (max-width: 768px) {
    padding: 30px 20px;
    flex-direction: column;
  }
  @media (max-width: 480px) {
    width: 100%;
    flex-direction: row;
    border-bottom: 1px solid #ececec;
    &:last-child {
      border-bottom: none;
    }
  }
  &:nth-child(1),
  &:nth-child(2),
  &:nth-child(4),
  &:nth-child(5) {
    &:before {
      content: '';
      width: 1px;
      height: 70%;
      display: block;
      background-color: #ececec;
      position: absolute;
      right: 0;
      top: 15%;
      @media (max-width: 480px) {
        display: none;
      }
    }
  }
  &:nth-child(2):before, &:nth-child(4):before {
    @media (max-width: 1024px) {
      display: none;
    }
  }
  &:nth-child(3):before {
    @media (max-width: 1024px) {
      content: '';
      width: 1px;
      height: 70%;
      display: block;
      background-color: #ececec;
      position: absolute;
      right: 0;
      top: 15%;
      @media (max-width: 480px) {
        display: none;
      }
    }
  }
  &:nth-child(1),
  &:nth-child(2),
  &:nth-child(3) {
    &:after {
      content: '';
      height: 1px;
      width: 80%;
      display: block;
      background-color: #ececec;
      position: absolute;
      bottom: 0;
      left: 10%;
      @media (max-width: 480px) {
        display: none;
      }
    }
  }
  &:nth-child(4):after {
    @media (max-width: 1024px) {
      content: '';
      height: 1px;
      width: 80%;
      display: block;
      background-color: #ececec;
      position: absolute;
      bottom: 0;
      left: 10%;
    }
    @media (max-width: 480px) {
      display: none;
    }
  }
  &_icon {
    margin-right: 30px;
    min-width: 73px;
    transition: all 0.8s ease;
    @media (max-width: 1200px) {
      min-width: 60px;
    }
    @media (max-width: 768px) {
      margin-bottom: 30px;
    }
    @media (max-width: 480px) {
      margin-bottom: 0px;
      min-width: 40px;
      max-width: 40px;
    }
  }
  &_text {
    color: #333;
    font-size: 1rem;
    line-height: 137%;
    @media (max-width: 1200px) {
      font-size: 15px;
    }
    @media (max-width: 768px) {
      font-size: 14px;
    }
  }
  &:hover {
    .advantages-item_icon {
      transform: rotateY(360deg);

    }
  }
}

