  width: 60%;
  position: relative;
  z-index: 25;
  @media (max-width: 1024px) {
    width: 100%;
  }
  &_input-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 40px;
    @media (max-width: 600px) {
      display: block;
    }
  }
  .input {
    width: 48%;
    margin-bottom: 20px;
    @media (max-width: 600px) {
      width: 100%;
    }
  }
  .input:last-child {
    width: 100%;
  }
  textarea {
    height: 122px;
    border: 1px solid #fff;
    @media (max-width: 480px) {
      height: 80px;
    }
  }
  input {
    height: 54px;
    border: 1px solid #fff;
    @media (max-width: 480px) {
      height: 40px;
    }
  }
  &_part-1 {
    opacity: 0;
    visibility: hidden;
    &.active {
      opacity: 1;
      visibility: visible;
    }
    .cost-form_input-wrap {
      min-height: 476px;
    }
  }
  &_part-2 {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    &.active {
      opacity: 1;
      visibility: visible;
    }
    .cost-form_input-wrap {
      min-height: 476px;
    }
  }
  .cost-form-btn {
    width: 315px;
    height: 60px;
    &:hover {
      background-color: $red;
    }
    @media (max-width: 480px) {
      width: 100%;
      height: 46px;
    }
  }
}

.cost-form_btn-wrap {
  display: flex;
  @media (max-width: 1024px) {
    display: block;
  }
}

.cost-form-btn {
  width: 390px;
  height: 68px;
  background-color: $red;
  color: #fff;
  border: none;
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: uppercase;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: default;
  text-decoration: none;
  @media (max-width: 480px) {
    width: 100%;
    height: 44px;
  }
  &.disabled {
    opacity: 0.5;
    &:hover {
      background-color: $red;
    }
  }
  &:hover {
    background-color: darken($red, 10%);
  }
}
.cost-form-btn-next {
  width: 315px;
  height: 60px;
  background-color: transparent;
  color: #fff;
  border: 3px solid $red;
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: uppercase;
  transition: all 0.2s ease;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  &:after {
    content: '';
    width: 28px;
    height: 10px;
    background-image: url(../images/sprite.svg#arrow-next);
    background-repeat: no-repeat;
    background-position: center;
    display: block;
    margin-left: 15px;
  }
  &:hover {
    border-color: darken($red, 15%);
  }
  @media (max-width: 480px) {
    width: 100%;
    height: 46px;
    border: 2px solid $red;
  }
}

.cost-form-btn-prev {
  width: 315px;
  height: 60px;
  margin-right: 25px;
  background-color: transparent;
  color: #fff;
  border: 3px solid $red;
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: uppercase;
  transition: all 0.2s ease;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  @media (max-width: 1024px) {
    margin-right: 0;
    margin-bottom: 15px;
  }
  &:before {
    content: '';
    width: 28px;
    height: 10px;
    background-image: url(../images/sprite.svg#arrow-next);
    background-repeat: no-repeat;
    background-position: center;
    display: block;
    margin-right: 15px;
    transform: rotate(180deg);
  }
  &:hover {
    border-color: darken($red, 15%);
  }
  @media (max-width: 480px) {
    width: 100%;
    height: 46px;
    border: 2px solid $red;
  }
}

.form-dots {
  display: flex;
  margin-bottom: 50px;
  &_item {
    width: 10px;
    height: 10px;
    background-color: #fff;
    margin-right: 30px;
    border-radius: 50%;
    @media (max-width: 480px) {
      width: 6px;
      height: 6px;
      margin-right: 20px;
    }
    &.active {
      box-shadow: 0px 0px 15px rgba(255, 255, 255, 0.95);
    }
  }
}

.map {
  height: 800px;
  position: relative;
  @media (max-width: 768px) {
    height: auto;
    display: flex;
    flex-direction: column-reverse;
  }
  &_img {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    object-fit: cover;
    @media (max-width: 768px) {
      position: relative;
      height: 400px;
    }
    @media (max-width: 768px) {
      height: 300px;
    }
  }
  &_wrap {
    position: relative;
    z-index: 30;
    display: flex;
    justify-content: flex-end;
    padding-top: 110px;
    @media (max-width: 768px) {
      padding-top: 20px;
      padding-bottom: 20px;
      display: block;
    }
  }
}
.contact-block {
  background-color: #fff;
  max-width: 511px;
  padding: 90px 74px;
  position: relative;
  @media (max-width: 1024px) {
    padding: 70px 50px;
    max-width: 462px;
  }
  @media (max-width: 768px) {
    max-width: 100%;
    width: 100%;
  }
  @media (max-width: 480px) {
    padding: 50px 40px;
  }
  &_title {
    font-size: 1.875rem;
    color: #000;
    margin-bottom: 55px;
    @media (max-width: 480px) {
      margin-bottom: 40px;
    }
  }
  &_description {
    font-size: 1rem;
    position: relative;
    padding-top: 15px;
    padding-right: 40px;
    margin-bottom: 120px;
    @media (max-width: 480px) {
      margin-bottom: 60px;
      padding-right: 0;
    }
    &:before {
      content: '';
      width: 74px;
      height: 4px;
      background-color: $red;
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      @media (max-width: 480px) {
        height: 2px;
      }
    }
  }
  .cost-form-btn {
    width: 362px;
    @media (max-width: 768px) {
      width: 280px;
    }
    @media (max-width: 480px) {
      width: 100%;
    }
  }
}

.animation-img {
  overflow: hidden;
  width: 100%;
  height: 1300px;
  position: absolute;
  z-index: 20;
  left: 0;
  // top: 5800px;
  bottom: 1000px;
  @media (max-width: 1600px) {
    // top: 5500px;
    right: -100px;
  }
  @media (max-width: 1300px) {
    display: none;
  }
}
.plane {
  position: absolute;
  right: -330px;
  top: 345px;
  z-index: 10;
  transform-origin: center;
  @media (max-width: 1600px) {
    right: -430px;
  }
}
.ship {
  position: absolute;
  right: -620px;
  top: 35px;
  z-index: 10;
  transform-origin: center;
  @media (max-width: 1600px) {
    right: -720px;
  }
}
