.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  color: #fff;
  padding-left: 295px;
  padding-right: 50px;
  padding-top: 34px;
  @media (max-width: 1915px) {
    padding-left: 100px;
  }
  @media (max-width: 480px) {
    padding-left: 60px;
    padding-top: 10px;
    padding-right: 10px;
  }
}

.h-contacts {
  display: flex;
  align-items: center;
  &_item {
    margin-right: 90px;
    @media (max-width: 1400px) {
      margin-right: 40px;
    }
    @media (max-width: 1200px) {
      display: none;
    }
    svg {
      fill: $red;
      margin-right: 15px;
    }
  }
  a, span {
    font-weight: 500;
    font-size: 1rem;
    color: #fff;
    &:hover {
      text-decoration: none;
    }
  }
  a[href^="tel:"] {
    text-decoration: none;
  }
}

.btn-red {
  background-color: $red;
  color: #fff;
  text-decoration: none;
  font-size: 13px;
  font-weight: 400;
  border-radius: 14px;
  width: 147px;
  height: 37px;
  border: 2px solid $red;
  padding: 5px 5px 8px 5px;
  transition: all 0.2s ease;
  @media (max-width: 480px) {
    width: 120px;
    height: 30px;
    padding: 3px 3px 7px 3px;
  }
  &:hover {
    background: transparent;
  }
}

.h-logo {
  @media (max-width: 480px) {
    width: 100px;
  }
}
