* {
	outline: none;
	box-sizing: border-box;
}
*::before, *::after {
	box-sizing: border-box;
}
html {
	font-size: 16px;
	@media (max-width: 1024px) {
		font-size: 15px;
	}
	@media (max-width: 768px) {
		font-size: 14px;
	}
	@media (max-width: 480px) {
		font-size: 12px;
	}
}
body {
	// background-color: #E5E5E5;
	background-color: #f1f3f6;
	font-family: 'TTInterfaces', sans-serif;
	font-weight: 300;
	margin: 0;
	min-height: 100vh;
	font-size: 1rem;
	display: flex;
	flex-direction: column;
	padding-left: 83px;
	scroll-behavior: smooth;

	position: relative;
	@media (max-width: 480px) {
		padding-left: 40px;
	}
}
.main-inner {
	flex-grow: 1;
	background-color: #F9FAFD;
}
ul[class], ol[class] {
	list-style: none;
	padding: 0;
	margin: 0;
}
h1[class], h2[class], h3[class], h4[class], h5[class] {
	margin-top: 0;
}
.uppercase {
	text-transform: uppercase;
}
img {
	max-width: 100%;
	display: block;
}
input, button, textarea, select {
	font: inherit;
}

.container {
	max-width: $container;
	width: 100%;
	margin: 0 auto;
}
.content-wrap {
  max-width: $content;
	width: 100%;
  margin: 0 auto;
	padding: 0 20px;
	@media (max-width: 340px) {
		padding: 0 10px;
	}
}
.no-scroll {
	overflow: hidden;
}

.link {
  color: $red;
  &:hover {
    text-decoration: none;
  }
}


.nav-icon {
	cursor: pointer;
	padding: 31px 28px;
	width: 83px;
	height: 83px;
	transition: all 0s ease;
	@media (max-width: 480px) {
		width: 40px;
		height: 40px;
		padding: 15px 7px;
	}
  &.active {
		@media (min-width: 480px) {
			opacity: 0.2;
		}
		@media (max-width: 480px) {
			.nav-icon_line {
				&:nth-child(1) {
					transform: rotate(45deg) translate(1px, 3px);
				}
				&:nth-child(2) {
					display: none;
				}
				&:nth-child(3) {
					transform: rotate(-45deg) translate(1px, -5px);
				}
			}
		}
  }
	&_line {
		width: 27px;
		height: 2px;
		margin-bottom: 5px;
		background-color: #000;
    transition: all 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
		&:last-child {
      margin-bottom: 0;
    }
	}
}

.slick-dots {
	display: flex;
	justify-content: flex-start;
	width: 100%;
	li {
		width: 10px;
		height: 10px;
		background-color: transparent;
		margin-right: 70px;
		border-radius: 50%;
		border: none;
		background: #FFFFFF;
		cursor: pointer;
		&:last-child {
			margin-right: 0;
		}
		&.slick-active {
			box-shadow: 0px 0px 15px rgba(255, 255, 255, 0.95);
		}
		@media (max-width: 768px) {
			width: 8px;
			height: 8px;
			margin-right: 10px;
		}
		@media (max-width: 480px) {
			width: 6px;
			height: 6px;
			margin-right: 8px;
		}
	}
	button {
		font-size: 0;
		line-height: 0;
		padding: 0;
		display: none;
	}
}

.menu {
	background: #fff;
	position: fixed;
	max-width: 83px;
	height: 100%;
	left: 0;
	top: 0;
	z-index: 40;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	overflow: hidden;
	transition: all 0.2s ease;
	@media (max-width: 480px) {
		max-width: 40px;
	}
	&.active {
		max-width: 238px;
		width: 238px;
		@media (max-width: 480px) {
			max-width: 100%;
			width: 100%;
		}
	}
}

.social-icon {
	&_item {
		width: 83px;
		height: 83px;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: $red;
		transition: all 0.2s ease;
		@media (max-width: 480px) {
			width: 40px;
			height: 40px;
		}
		&:hover {
			background-color: darken($red, 10%);
		}
		&:before {
			content: '';
			display: block;
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			@media (max-width: 480px) {
				transform: scale(0.8);
			}
		}
		&.fb:before {
			width: 8px;
			height: 18px;
			background-image: url(../images/sprite.svg#fb);
		}
		&.vk:before {
			width: 22px;
			height: 13px;
			background-image: url(../images/sprite.svg#vk);
		}
		&.inst:before {
			width: 17px;
			height: 17px;
			background-image: url(../images/sprite.svg#inst);
		}
	}
}

.nav {
	// width: 83px;
	// overflow: hidden;
	padding-left: 15px;
	padding-right: 15px;
	@media (max-width: 480px) {
		padding-left: 0px;
		padding-right: 0px;
	}
	&_item {
		border-bottom: 1px solid #E2E4E9;
		// padding: 13px 20px;
		&:hover {
			.nav_link {
				color: $red;
			}
		}
		&:last-child {
			border-bottom: none;
		}
	}
	&_link {
		color: #000;
		font-size: 14px;
		font-weight: 600;
		text-decoration: none;
		white-space: nowrap;
		display: flex;
		align-items: center;
		span {
			width: 53px;
			min-width: 53px;
			height: 53px;
			// background-color: #333;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-right: 20px;
			@media (max-width: 480px) {
				width: 40px;
				min-width: 40px;
				height: 48px;
			}
			&:before {
				content: '';
				background-size: cover;
				display: block;
				background-position: center;
			}
		}
	}
}

.services-icon:before {
	width: 20px;
	min-width: 20px;
	height: 20px;
	background-image: url(../images/sprite.svg#services);
}
.about-icon:before {
	width: 17px;
	min-width: 17px;
	height: 16px;
	background-image: url(../images/sprite.svg#about);
}
.news-icon:before {
	width: 16px;
	min-width: 16px;
	height: 16px;
	background-image: url(../images/sprite.svg#news);
}
.info-icon:before {
	width: 16px;
	min-width: 16px;
	height: 16px;
	background-image: url(../images/sprite.svg#info);
}
.vacancies-icon:before {
	width: 16px;
	min-width: 16px;
	height: 16px;
	background-image: url(../images/sprite.svg#vacancies);
}
.contacts-icon:before {
	width: 16px;
	min-width: 16px;
	height: 16px;
	background-image: url(../images/sprite.svg#contacts);
}

.fade-in {
	opacity: 0;
	transition: all 0.4s ease;
	transform: translateY(40px);
	&.active {
		opacity: 1;
		transform: translateY(0);
	}
}

.fade-in-opacity {
	opacity: 0;
	transition: all 0.4s ease;
	&.active {
		opacity: 1;
	}
}

.btn-fix {
	background-color: #000;
	color: #fff;
	font-size: 14px;
	border-radius: 5px 5px 0px 0px;
	width: 334px;
	height: 41px;
	position: fixed;
	bottom: 0;
	right: 35px;
	border: 1px solid #000;
	z-index: 30;
	display: flex;
	align-items: center;
	justify-content: center;
	@media (max-width: 1024px) {
		display: none;
	}
	&:before {
		content: '';
		width: 17px;
		min-width: 17px;
		height: 16px;
		background-image: url(../images/sprite.svg#mail);
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		display: block;
		margin-right: 10px;
	}
	&:hover {
		background-color: rgba(0,0,0,0.8);
	}
}


.breadcrumbs {
	padding: 10px;
	padding-left: 0;
	margin-bottom: 70px;
	margin-left: -20px;
	@media (max-width: 480px) {
		margin-bottom: 35px;
		margin-left: -5px;
	}
	&_list {
		display: flex;
		flex-wrap: wrap;
	}
	&_item {
		text-decoration: none;
		color: #fff;
		font-size: 0.875rem;
		padding: 0px 20px 0 15px;
		position: relative;
		display: block;
		@media (max-width: 480px) {
			padding: 0 10px 0 5px;
		}
		&:after {
			content: '>';
			display: block;
			position: absolute;
			right: 0;
			top: 0px;
			color: $yellow;
		}
		&:last-child {
			pointer-events: none;
			color: $yellow;
			&:after {
				display: none;
			}
		}
	}
}
