.footer {
  background-color: #000;
  padding-top: 56px;
}
.footer-top {
  padding-bottom: 35px;
  border-bottom: 1px solid rgba(117,117,117, 0.4);
  display: flex;
  @media (max-width: 1024px) {
    display: block;
  }
  &_left {
    border-right: 1px solid rgba(117,117,117, 0.4);
    padding-right: 75px;
    width: 32%;
    @media (max-width: 1024px) {
      width: 100%;
      border-bottom: 1px solid rgba(117,117,117, 0.4);
      border-right: none;
      padding-bottom: 20px;
    }
  }
  &_right {
    width: 68%;
    padding-left: 75px;
    @media (max-width: 1024px) {
      width: 100%;
      padding-left: 0px;
      padding-top: 20px;
    }
  }
}
.f-logo {
  margin-bottom: 30px;
  display: block;
}
.f-contacts {
  color: #fff;
  font-size: 0.875rem;
  @media (max-width: 480px) {
    font-size: 12px;
  }
  &_item {
    margin-bottom: 5px;
    a {
      color: #fff;
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.f-title {
  color: #fff;
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 25px;
  @media (max-width: 480px) {
    font-size: 15px;
    margin-bottom: 20px;
  }
}
.f-nav {
  column-count: 3;
  @media (max-width: 768px) {
    column-count: 2;
  }
  &_link {
    font-size: 0.813rem;
    color: #fff;
    text-decoration: none;
    font-weight: 300;
    display: block;
    margin-bottom: 10px;
    @media (max-width: 768px) {
      margin-bottom: 5px;
    }
    @media (max-width: 480px) {
      font-size: 12px;
    }
    &:hover {
      color: $red;
      text-decoration: underline;
    }
  }
}

.footer-bottom {
  color: #fff;
  color: #ADADAD;
  padding: 23px 0px 29px 0;
  font-size: 0.813rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 480px) {
    display: block;
    font-size: 11px;
  }
}

.dev {
  display: flex;
  align-items: center;
  color: #ADADAD;
  @media (max-width: 480px) {
    margin-top: 10px;
  }
  &_text {
    margin-right: 20px;
    a {
      color: #ADADAD;
      &:hover {
        text-decoration: none;
      }
    }
  }
  &_logo {
    @media (max-width: 480px) {
      display: none;
    }
  }
}
