.inner-head {
  height: 630px;
  background-color: #cdcdcd;
  padding-top: 170px;
  position: relative;
  @media (max-width: 480px) {
    padding-top: 120px;
    height: 550px;
  }
  &:before {
    content: '';
    // width: 373px;
    // height: 374px;
    width: 288px;
    height: 288px;
    display: block;
    background-image: url(../images/inner-head.svg);
    position: absolute;
    right: 50px;
    bottom: 50px;
    z-index: 5;
    background-size: cover;
    @media (max-width: 1200px) {
      width: 200px;
      height: 200px;
    }
    @media (max-width: 480px) {
      right: 10px;
      bottom: 10px;
      width: 100px;
      height: 100px;
    }
  }
  &_content {
    z-index: 10;
    position: relative;
  }
  &_title {
    color: #fff;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 3.5rem;
    margin-bottom: 40px;
    @media (max-width: 480px) {
      font-size: 30px;
    }
    @media (max-width: 340px) {
      font-size: 28px;
    }
  }
  &_description {
    font-size: 1.125rem;
    line-height: 125%;
    max-width: 600px;
    position: relative;
    padding-top: 15px;
    color: #fff;
    font-weight: 300;
    &:before {
      content: '';
      width: 64px;
      height: 3px;
      background-color: $red;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  &_img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 2;
    &:before {
      content: '';
      width: 100%;
      height: 100%;
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      background-color: rgba(0,0,0,0.3);
      z-index: 5;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &-desc {
      @media (max-width: 480px) {
        display: none;
      }
    }
    &-mobile {
      display: none;
      @media (max-width: 480px) {
        display: block;
      }
    }
  }
}

.inner-page {
  padding-top: 80px;
  padding-bottom: 90px;
  @media (max-width: 480px) {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}

.news-inner {
  margin-bottom: 60px;
  &_item {
    display: flex;
    align-items: center;
    padding-top: 30px;
    padding-bottom: 30px;
    border-bottom: 1px solid #ECECEC;
    @media (max-width: 768px) {
      display: block;
    }
    &:last-child {
      border-bottom: none;
    }
  }
  &_img {
    width: 340px;
    min-width: 340px;
    height: 315px;
    background-color: #cfcfcf;
    @media (max-width: 1024px) {
      width: 280px;
      min-width: 280px;
    }
    @media (max-width: 768px) {
      width: 340px;
      min-width: 340px;
      margin-bottom: 30px;
    }
    @media (max-width: 480px) {
      width: 100%;
      min-width: 100%;
      height: 240px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &_text {
    padding-left: 72px;
    @media (max-width: 1024px) {
      padding-left: 30px;
    }
    @media (max-width: 768px) {
      padding-left: 0;
    }
  }
  &_link {
    color: #A4A4A4;
    font-size: 1rem;
    display: inline-block;
    padding-bottom: 3px;
    border-bottom: 1px dashed #A4A4A4;
    text-decoration: none;
    &:after {
      content: '';
      width: 6px;
      height: 10px;
      background-image: url(../images/sprite.svg#arrow-link);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      display: inline-block;
      margin-left: 6px;
    }
    &:hover {
      color: darken(#A4A4A4, 20%);
    }
  }
  .news-item_title {
    max-width: 600px;
    margin-bottom: 25px;
  }
  .news-item_description {
    margin-bottom: 20px;
  }
}


.page-nav {
