.inner-title {
  font-size: 1.875rem;
  line-height: 120%;
  text-transform: uppercase;
  margin-bottom: 45px;
}

.accordion {
  min-height: 700px;
  @media (max-width: 480px) {
    min-height: 0;
  }
  &_item {
    margin-bottom: 22px;
    @media (max-width: 480px) {
      margin-bottom: 15px;
    }
  }
  &--white {
    .accordion-head {
      background-color: #F0F1F2;
      font-size: 1.5rem;
      @media (max-width: 480px) {
        font-size: 1.375rem;
      }
      &:after {
        width: 43px;
        height: 43px;
        background-color: $red;
        border-radius: 50%;
        margin-top: -21px;
        margin-left: -21px;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.15);
        background-image: url(../images/arrow-down.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        @media (max-width: 768px) {
          width: 30px;
          height: 30px;
          margin-left: -15px;
          margin-top: -15px;
          right: 15px;
        }
      }
    }
  }
}
.accordion-head {
  padding: 31px 100px 31px 56px;
  background-color: $yellow;
  color: #000;
  font-size: 1.125rem;
  font-weight: 600;
  display: flex;
  justify-content: space-between;
  position: relative;
  @media (max-width: 1024px) {
    display: block;
  }
  @media (max-width: 768px) {
    padding: 30px 50px 30px 20px;
  }
  @media (max-width: 480px) {
    padding: 20px 50px 20px 20px;
  }
  &:after {
    content: '';
    width: 18px;
    height: 9px;
    background-image: url(../images/sprite.svg#arrow-accordion);
    background-size: cover;
    background-position: center;
    display: block;
    position: absolute;
    top: 50%;
    margin-top: -4px;
    right: 56px;
    transition: all 0.2s ease;
    @media (max-width: 768px) {
      right: 20px;
      top: 50%;
      margin-top: -4px;
    }
  }
  &.active {
    &:after {
      transform: rotate(180deg);
    }
  }
  &_left {
    @media (max-width: 1024px) {
      margin-bottom: 8px;
    }
  }
  &_right {
    @media (max-width: 1024px) {
      font-size: 1rem;
    }
  }
}
.accordion-body {
  background-color: #fff;
  padding: 20px 56px;
  @media (max-width: 480px) {
    padding: 10px 20px;
  }
  &_title {
    color: #333333;
    font-size: 1.125rem;
    font-weight: 600;
  }
  &_block {
    border-bottom: 1px solid #ECECEC;
    padding-bottom: 30px;
    padding-top: 30px;
    @media (max-width: 480px) {
