    padding-top: 30px;
    padding-right: 300px;
    position: relative;
    @media (max-width: 768px) {
      padding-right: 0;
    }
  }
}
.accordion-btn {
  width: 267px;
  height: 60px;
  background-color: $red;
  color: #fff;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  border: none;
  text-decoration: none;
  transition: all 0.2s ease;
  position: absolute;
  right: 0;
  top: 60px;
  @media (max-width: 768px) {
    position: relative;
    top: 0;
    margin-top: 30px;
  }
  @media (max-width: 480px) {
    width: 200px;
    height: 44px;
    font-size: 12px;
    margin-top: 20px;
  }
  &:hover {
    background-color: darken($red, 10%);
  }
}

